import * as React from 'react';
import ImgYear15 from "../../components/defaults/studioImg/imgYear15"
import ImgYear16 from "../../components/defaults/studioImg/imgYear16"
import ImgYear17 from "../../components/defaults/studioImg/imgYear17"
import ImgYear18 from "../../components/defaults/studioImg/imgYear18"
import ImgYear19 from "../../components/defaults/studioImg/imgYear19"
import InfiniteScroll from 'react-infinite-scroll-component';
import { Player } from 'video-react';


const Year = ({ year, imageData }) => {

    const [images, setImages] = React.useState([]);
    const [count, setCount] = React.useState(20);

    const component = {
        15: ImgYear15,
        16: ImgYear16,
        17: ImgYear17,
        18: ImgYear18,
        19: ImgYear19,
    }

    const CurrentYear = component[year];

    React.useEffect(() => {
        if (count === 20) {
            setImages(imageData.slice(0, count))
        }
        else {
            setTimeout(() => {
                setImages(imageData.slice(0, count))
            }, 1500);
        }
    }, [count])


    function fetchMore() {
        if (count + 12 > imageData.length) {
            let finalCount = imageData.length - count;
            setCount(count + finalCount)
        }
        else {
            setCount(count + 12);
        }
    }

    return (
        <InfiniteScroll
            className="year-album"
            dataLength={images.length}
            next={fetchMore}
            hasMore={count < imageData.length}
            loader={count <= imageData.length && count !== 0 && <div className="loader" key={0}>
                <div className="lds-dual-ring" />
            </div>
            }>
            {
                images.map((item, index) => <div key={index} className="year-images" style={{ width: item.width }}>
                    {
                        item.type === "video" ? <Player autoPlay
                            loop muted
                        >
                            <source src={`${require(`../video/${item.name}.mp4`)}`} type="video/mp4" />
                        </Player> : <CurrentYear image={item.name} />
                    }
                </div>)
            }
        </InfiniteScroll>
    );
}

export default Year;